.pattern-view-wrap{display:flex;flex-direction:column;align-items:center;position:relative}.pattern-view-wrap .key-patterns{flex-direction:row;display:flex}.pattern-view-wrap .key-patterns .pattern{width:60px;height:60px;font-size:28px;align-items:center;justify-content:center;display:flex;margin:5px}.pattern-view-wrap .key-patterns .pattern.ready{color:#404040;background-color:white;border:solid 4px #404040}.pattern-view-wrap .key-patterns .pattern.success{background-color:#808080;border:solid 4px #404040}.pattern-view-wrap .key-patterns .pattern.current{color:#fcc419;background-color:white;border:solid 4px #fcc419}.pattern-view-wrap .timer-progress{width:400px;margin-top:30px}.pattern-view-wrap .message{position:absolute;display:flex;top:100%;margin-top:40px;font-size:18px;font-weight:500;color:white}.pattern-view-wrap .message.success{color:#1c7ed6}.pattern-view-wrap .message.failure{color:#f03e3e}
